.cart {
  display: flex;
  justify-content: space-between;
  margin-top: 20px ;
  flex-wrap: wrap;
  gap: 20px;
  
  .prod {
    display: flex;
    flex-direction: column;
    gap: 15px;
    @media (max-width: 1200px) {
      margin: 0 auto;
    }
    .box {
      background-color: #eee;
      padding: 15px;
      
      .info{
        display: flex;
        justify-content: space-between;
        gap: 20px;
        align-items: center;
        margin-bottom: 15px;
        h4 {
          margin: 0;
        }
        p {
          margin: 0;
        }
      }
      .options {
        display: flex;
        justify-content: space-between;
        button {
          background-color: orangered;
          color: white;
          border: none;
          padding:  5px 10px;
          cursor: pointer;
        }
        select {
          cursor: pointer;
        }
      }
    }
  }
  .total {
    min-width: 300px;
    background-color: #eee;
    height: fit-content;
    padding: 20px;
    @media (max-width: 1200px) {
      margin: 0 auto;
    }
    p{
      margin: 0 0 15px 0; 
    }
    button {
      width: 100%;
      background-color: orange;
      color: white;
      border: none;
      padding: 5px;
      font-size: 15px;
      cursor: pointer;
    }
  }
}