.allProducts {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}
.allProducts .product {
  overflow: hidden;
  border: 2px #eee solid;
  padding: 10px;
  position: relative;
}
.allProducts .product img {
  max-width: 300px;
  height: 200px;
}
.allProducts .product .price {
  font-weight: bold;
}
.allProducts .product .more {
  position: absolute;
  bottom: 5px;
  right: 5px;
  font-size: 18px;
  font-weight: 500;
  display: flex;
  gap: 5px;
  align-items: center;
  color: rgb(25, 0, 255);
}
.allProducts .product .more .goMoreArr {
  margin-bottom: -4px;
}/*# sourceMappingURL=product.css.map */