.about {
  background-image: url(../img/05.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  height: calc(100vh - 85.875px);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
@media (max-width: 600px) {
  .about {
    height: calc(100vh - 138.875px);
  }
}
.about::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
}
.about .text {
  z-index: 2;
  color: white;
  max-width: 400px;
}
.about .text p {
  font-size: 18px;
}/*# sourceMappingURL=about.css.map */