form {
  display: flex;
  gap: 20px;
  margin: 100px 0;
}
@media (max-width: 768px) {
  form {
    flex-wrap: wrap;
  }
  form div {
    width: 100% !important;
  }
}
form div {
  display: flex;
  flex-direction: column;
  width: 50%;
  gap: 10px;
}
form div textarea {
  height: 140px;
}
form div * {
  height: 40px;
  padding: 10px 15px;
  font-size: 15px;
  border: 2px #888 solid;
  background-color: rgba(218, 218, 218, 0.19);
}
form div *:focus {
  outline-color: orange;
}
form div *:focus::-moz-placeholder {
  opacity: 0;
  transition-duration: 0.3s;
}
form div *:focus::placeholder {
  opacity: 0;
  transition-duration: 0.3s;
}
form div *.submit {
  background-color: orange;
  color: white;
  border: none;
  font-size: 15px;
  cursor: pointer;
}/*# sourceMappingURL=contact.css.map */