.prodDet {
  display: flex;
  padding: 50px;
  align-items: center;
  flex-wrap: wrap;
  text-align: center;
  justify-content: space-between;
  @media (max-width: 850px) {
    justify-content: center;
  }
  img {
    max-width: 300px;
  }
  div {
    text-align: center;
    position: relative;
    max-width: 450px;
    h3 {
      text-align: center;
    }
  }
  button {
    width: 350px;
    height: 40px;
    position: fixed;
    bottom: 0;
    left: calc(50% - 175px);
    background-color: #4CAF50;
    color: white;
    font-size: 25px;
    border: none;
    cursor: pointer;
  }
}