.prodDet {
  display: flex;
  padding: 50px;
  align-items: center;
  flex-wrap: wrap;
  text-align: center;
  justify-content: space-between;
}
@media (max-width: 850px) {
  .prodDet {
    justify-content: center;
  }
}
.prodDet img {
  max-width: 300px;
}
.prodDet div {
  text-align: center;
  position: relative;
  max-width: 450px;
}
.prodDet div h3 {
  text-align: center;
}
.prodDet button {
  width: 350px;
  height: 40px;
  position: fixed;
  bottom: 0;
  left: calc(50% - 175px);
  background-color: #4CAF50;
  color: white;
  font-size: 25px;
  border: none;
  cursor: pointer;
}/*# sourceMappingURL=productDetails.css.map */