.cart {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  flex-wrap: wrap;
  gap: 20px;
}
.cart .prod {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
@media (max-width: 1200px) {
  .cart .prod {
    margin: 0 auto;
  }
}
.cart .prod .box {
  background-color: #eee;
  padding: 15px;
}
.cart .prod .box .info {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  align-items: center;
  margin-bottom: 15px;
}
.cart .prod .box .info h4 {
  margin: 0;
}
.cart .prod .box .info p {
  margin: 0;
}
.cart .prod .box .options {
  display: flex;
  justify-content: space-between;
}
.cart .prod .box .options button {
  background-color: orangered;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}
.cart .prod .box .options select {
  cursor: pointer;
}
.cart .total {
  min-width: 300px;
  background-color: #eee;
  height: -moz-fit-content;
  height: fit-content;
  padding: 20px;
}
@media (max-width: 1200px) {
  .cart .total {
    margin: 0 auto;
  }
}
.cart .total p {
  margin: 0 0 15px 0;
}
.cart .total button {
  width: 100%;
  background-color: orange;
  color: white;
  border: none;
  padding: 5px;
  font-size: 15px;
  cursor: pointer;
}/*# sourceMappingURL=cart.css.map */